/* modules */
.advantages {
  display: flex;
  justify-content: space-between;
  &__block {
    display: flex;
    align-items: center;
    white-space: nowrap;
    font-size: 15px;
    svg {
      display: block;
      width: 40px;
      height: 40px;
      margin-right: 10px;
      flex-shrink: 0;
      path {
        fill: $primary-color; } }
    span {
      display: inline-block;
      line-height: 17px; } }
  &>div {
    &+div {
      padding-left: 15px; } }
  &--why {
    background: rgba($color-white,.45);
    padding: 15px 25px;
    &>div {
      flex-grow: 1;
      &+div {
        padding-left: 30px;
        border-left: 1px solid #c5c5c5; } }
    .advantages__block {
      height: 62px; } } }

.main-slider {
  height: 100vh;
  flex-shrink: 0;
  .swiper-container {
    width: 100%;
    height: 100%;
    .swiper {
      &-slide {
        background-position: center;
        background-size: cover; }
      &-pagination {
        bottom: 30px;
        &-bullet {
          width: 10px;
          height: 10px;
          opacity: 1; } } } }
  .slick-dots {
    bottom: 25px;
    li {
      width: auto;
      height: auto;
      vertical-align: middle;
      button {
        padding: 0;
        width: 10px;
        height: 10px;
        background: $color-black;
        border-radius: 50%;
        &::before {
          display: none; } }
      &.slick-active {
        button {
          background: $color-white; } } } }
  &__block {
    height: 100vh;
    padding-top: 95px;
    overflow: hidden;
    font-size: 14px;
    background: {
      position: top center;
      size: cover;
      repeat: no-repeat; }
    .container {
      position: relative;
      height: calc(100% - 95px); }
    p {
      line-height: 22px; } }
  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%);
    padding-left: 15px; }
  &__title {
    font-family: $font-exo2-r;
    font-size: 35px;
    line-height: 1.2;
    color: #333;
    margin-bottom: 5px; }
  &__img {
    &--1 {
      margin-left: 15px; } }
  &__btn {
    text-decoration: none;
    height: 40px; }

  &Top {
    .slick-dots {
      bottom: 25px;
      li {
        width: auto;
        height: auto;
        vertical-align: middle;
        button {
          padding: 0;
          width: 10px;
          height: 10px;
          background: $color-black;
          border-radius: 50%;
          border: 1px solid $color-black;
          &::before {
            display: none; } }
        &.slick-active {
          button {
            background: transparent; } } } }
    &__block {
      position: relative;
      overflow: hidden;
      background: $color-white; }
    &__content {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateY(-50%);
      right: 0;
      overflow: hidden;
      padding-right: 15px;
      p {
        max-width: 500px; } }
    &__img {
      @include clearfix;
      img {
        width: 100%; } } } }

.section {
  &__head {
    display: table;
    font-size: 35px;
    margin: 0 auto 30px;
    &:before, &:after {
      content: '';
      display: table-cell;
      background-image: linear-gradient($color-ccc, $color-ccc);
      background-size: 100% 1px;
      background-repeat: no-repeat;
      background-position: center;
      width: 145px; }
    &>div {
      display: table-cell;
      white-space: nowrap;
      text-align: center;
      width: 100px;
      padding: 0 20px;
      position: relative;
      h1, .h1 {
        font-size: 35px;
        margin-bottom: 0; } } } }

.why {
  background: radial-gradient(circle,rgba(255,255,255,0) 0%,#d3d3d3 40%); }

.main-category {
  padding: 40px 0;
  &__wrapper {
    position: relative;
    height: 556px;
    &--row {
      height: 268px; } }
  &__block {
    position: absolute;
    width: 556px;
    a {
      display: block;
      img {
        max-width: 100%;
        display: block;
        height: auto;
        opacity: 1;
        transition: opacity 0.35s;
        &:hover {
          opacity: 0.7; } } }
    &--1, &--4, &--7 {
      left: 0;
      top: 0; }
    &--2, &--8 {
      top: 0;
      right: 0; }
    &--3, &--6 {
      right: 0;
      bottom: 0; }
    &--5 {
      bottom: 0;
      left: 0; } } }

.subscription {
  &__wrapper {
    background: #fef6e9;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 110px;
    padding: 0;
    border-style: solid;
    border-width: 7px 7px 7px 7px;
    border-image: url('../img/subscription-border.png') 7 repeat;
    position: relative; }
  &__body {
    display: flex;
    align-items: center; }
  &__input {
    width: 263px;
    background: $color-white;
    border: 1px solid #cfcfcf;
    padding: 0 44px 0 9px;
    height: 35px; }
  &__label {
    padding-right: 20px;
    font-size: 17px;
    font-family: $font-exo2-r;
    line-height: 20px;
    strong {
      font-family: $font-exo2-b; } }
  &__btn {
    width: 35px;
    height: 35px;
    text-align: center;
    border: 0;
    background: #25925e;
    color: $color-white;
    line-height: 35px;
    margin-top: -35px;
    position: absolute;
    right: 0;
    border-radius: 5px;
    font-size: 14px;
    transition: background 0.35s;
    &:hover, &:focus {
      background: $color-green; } }
  &__form {
    position: relative;
    overflow: hidden;
    border-radius: 0 5px 5px 0;
    &>.form-group {
      margin-bottom: 0; } }
  &__thank {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #fef6e9;
    z-index: 100;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    text-align: center;
    &.is-active {
      opacity: 1;
      visibility: visible; }
    &>div {
      padding: 10px; } } }

.news {
  padding-bottom: 40px;
  &__block {
    font-size: 12px;
    margin-bottom: 25px;
    &-img {
      overflow: hidden;
      margin-bottom: 5px; }
    &-title {
      font-size: 16px;
      font-family: $font-exo2-r;
      text-decoration: underline;
      line-height: 18px;
      margin-bottom: 5px;
      color: $primary-color;
      display: inline-block;
      &:hover {
        color: $color-green;
        text-decoration: underline; } }
    p {
      line-height: 16px; } }
  &__date {
    margin-bottom: 15px;
    font-size: 14px;
    margin-top: -10px; } }

.row__flex {
  display: flex;
  flex-wrap: wrap;
  &--center {
    justify-content: center; } }

.breadcrumbs {
  margin: -10px 0 15px 0;
  padding: 0;
  font-size: 14px;
  font-family: $font-roboto-m;
  li {
    list-style: none;
    margin: 0 0 5px 0;
    display: inline-block;
    &+li {
      &::before {
        content: '/';
        display: inline-block;
        margin-right: 4px; } }
    a {
      color: $color-666;
      text-decoration: none;
      &:hover {
        color: $color-green; } } } }

.shared {
  border: 1px solid #eee;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $color-666;
  padding: 16px 15px;
  &>div {
    &+div {
      padding-left: 15px; } }
  &__label {
    text-transform: uppercase; }
  &__social {
    a {
      display: inline-block;
      text-decoration: none;
      &+a {
        margin-left: 20px; }
      &:hover {
        i {
          background: $color-green;
          border-color: $color-green;
          color: $color-white; } } }
    i {
      display: inline-block;
      height: 30px;
      line-height: 30px;
      width: 30px;
      border-radius: 30px;
      border: 1px solid #666666;
      color: #666666;
      text-align: center;
      font-size: 14px;
      background: none;
      transition: all 0.35s; } } }

.delivery {
  &__block {
    position: relative;
    font-size: 12px;
    padding-left: 65px;
    margin-bottom: 25px;
    &-img {
      position: absolute;
      left: 0;
      top: 0;
      width: 55px;
      height: 55px;
      background: #e9f6ef;
      svg {
        width: 37px;
        height: 37px;
        position: absolute;
        left: 10px;
        top: 10px;
        .path-color1 {
          fill: $primary-color; }
        .path-color2 {
          fill: #ed1c24; } } }
    &>p {
      margin: 0;
      line-height: 17px; }
    &-title {
      color: $color-green;
      font-size: 16px;
      font-family: $font-roboto-m;
      margin-bottom: 5px;
      padding-right: 25px; }
    &-info {
      position: absolute;
      right: 0;
      top: 4px;
      .fas {
        color: $color-green;
        font-size: 14px;
        cursor: pointer;
        display: block; }
      &-popup {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        font-size: 12px;
        bottom: 0;
        width: 285px;
        left: 50%;
        transform: translateX(-50%) translateY(-35px);
        background: $color-white;
        z-index: 100;
        padding: 20px;
        box-shadow: 0 0 20px rgba($primary-color,.8);
        color: $color-666;
        transition: all 0.35s;
        transition-delay: 0.15s;
        max-height: 250px;
        overflow: hidden;
        overflow-y: auto;
        p {
          line-height: 17px;
          &:last-child {
            margin-bottom: 0; } }
        ul {
          padding-left: 15px;
          li {
            &:not(:last-child) {
              margin-bottom: 5px; } } } }
      &:hover {
        .delivery__block-info-popup {
          transform: translateX(-50%) translateY(-25px);
          opacity: 1;
          visibility: visible; } } } } }

.contact {
  &__block {
    padding: 20px;
    border: 1px solid #ecedea; }
  &__info-block {
    font-size: 14px;
    &-title {
      font-size: 17px;
      font-family: $font-roboto-b;
      margin-bottom: 5px;
      .fas {
        display: inline-block;
        color: $color-green;
        margin-right: 5px;
        font-size: 14px;
        vertical-align: 2px; }
      small {
        font-size: 12px; } } } }

.tab {
  &-content {
    border: 1px solid $color-eee;
    padding: 20px;
    min-height: 220px; }
  &-nav {
    font-size: 16px;
    font-family: $font-exo2-r;
    padding: 0;
    margin: 0 0 20px 0;
    li {
      margin: 0 0 5px 0;
      list-style: none;
      a {
        display: block;
        border: 1px solid $color-eee;
        text-decoration: none;
        color: $primary-color;
        &:hover {
          background: $color-green;
          border-color: $color-green;
          color: $color-white; } }
      &.active {
        a {
          border-color: $color-green;
          color: $color-green;
          background: none; } } } } }
.table {
  color: $color-666; }
thead th {
  color: #999999;
  font-size: 12px;
  font-weight: 400;
  &[align="center"] {
    text-align: center; }
  &[valign="center"] {
    vertical-align: middle !important; } }
tbody {
  td {
    &[valign="center"] {
      vertical-align: middle !important; } }
  th {
    font-weight: 400;
    background: $color-eee;
    font-family: $font-roboto-m;
    text-transform: uppercase;
    color: $primary-color; } }

.panel {
  &-group {
    .panel {
      box-shadow: none;
      border-radius: 0; } }
  &-default {
    border-color: $color-eee;
    &>.panel-heading {
      background: #f5f5f5;
      border-color: $color-eee;
      &+.panel-collapse>.panel-body {
        border-top-color: $color-eee; } } }
  &-title {
    a {
      position: relative;
      text-decoration: none;
      display: inline-block;
      padding-left: 20px;
      .fas {
        font-size: 12px;
        position: absolute;
        left: 0;
        top: 6px;
        transform: rotate(0);
        transform-origin: 50% 50%; }
      &[aria-expanded="true"] {
        color: $color-green;
        .fas {
          transform: rotate(-180deg); } } } } }

.loader {
  &__model {
    display: flex;
    max-width: 710px;
    margin: 0 auto 20px;
    &>div {
      flex-grow: 1;
      &+div {
        padding-left: 30px;
        width: 370px;
        flex-shrink: 0; } }
    &-img {
      img {
        display: block;
        margin: 0 auto;
        max-width: 100%; } } }
  &__download {
    &-title {
      font-family: $font-exo2-r;
      font-size: 22px;
      margin-bottom: 25px;
      small {
        margin-top: 5px;
        color: #666666;
        font-size: 16px;
        font-family: $font-roboto-m;
        display: block; } }
    &-wrapper {
      margin-bottom: 30px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap; }
    &-block {
      width: 360px;
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      text-decoration: none;
      border: 1px solid #999999;
      position: relative;
      border-radius: 5px;
      flex-shrink: 0;
      height: 64px;
      padding-left: 75px;
      padding-right: 30px;
      position: relative;
      font-family: $font-roboto-m;
      color: $primary-color;
      font-size: 15px;
      &:nth-child(2n) {
        margin-left: 30px; }
      strong {
        font-family: $font-roboto-m;
        text-decoration: underline;
        display: inline-block;
        line-height: 1.2; }
      small {
        color: $color-666;
        font-size: 12px;
        display: block;
        transition: color 0.35s; }
      .fas {
        font-size: 14px;
        margin-left: 3px; }
      &:hover {
        background: $color-green;
        border-color: $color-green;
        color: $color-white;
        small {
          color: $color-white; }
        svg {
          .path-color {
            fill: $color-white; } } }
      svg {
        position: absolute;
        width: 50px;
        height: 50px;
        left: 15px;
        top: 6px;
        .path-color {
          transition: fill 0.35s;
          fill: $color-green; } } }
    &-img {
      margin-bottom: 20px;
      img {
        display: block;
        margin: 0 auto;
        max-width: 100%; } } } }

.catalog {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    &>div {
      padding: 0 15px;
      width: 25%; } }
  &__block {
    min-height: 450px;
    border: 1px solid #ecedea;
    box-shadow: 0 0 0 rgba($primary-color,0.2);
    color: $primary-color;
    margin-bottom: 30px;
    transition: all 0.5s;
    transform: translateY(0);
    position: relative;
    z-index: 20;
    &:hover {
      box-shadow: 2px 20px 30px rgba($primary-color,0.2);
      transform: translateY(-10px);
      z-index: 30;
      .catalog__block {
        &-img {
          img {
            &.img-default {
              opacity: 0; }
            &.img-hover {
              opacity: 1; } } }
        &-th {
          &-wrapper {
            box-shadow: 0px 22px 30px rgba($primary-color,0.2);
            opacity: 1;
            visibility: visible; } } } }
    &-img {
      height: 288px;
      border-bottom: 1px solid #ecedea;
      position: relative;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      &>a {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 100; }
      &>img {
        max-width: 100%;
        max-height: 100%;
        height: auto;
        display: block;
        transition: opacity 0.35s ease;
        &.img-first {
          opacity: 0; }
        &.img-default {
          position: absolute;
          opacity: 1; }
        &.img-hover {
          position: absolute;
          opacity: 0; } } }
    &-content {
      text-align: center;
      padding: 10px 10px 20px 10px; }
    &-name {
      text-decoration: none;
      color: $primary-color;
      font-family: $font-roboto-m;
      font-size: 16px;
      margin-bottom: 5px;
      display: inline-block;
      height: 44px;
      overflow: hidden; }
    &-rating {
      margin: 0 0 10px 0;
      font-size: 12px;
      padding: 0;
      li {
        list-style: none;
        margin: 0;
        display: inline-block;
        &.star-color {
          .fa-star {
            color: #ff7f00; } } }
      .fa-star {
        color: $color-999; } }
    &-price {
      margin-bottom: 20px;
      font-size: 18px;
      font-family: $font-roboto-m;
      small {
        font-family: $font-roboto-r;
        font-size: 12px; }
      del {
        color: #999999;
        text-decoration: line-through;
        &+strong {
          margin-left: 25px; } }
      &>* {
        display: inline-block; } }
    &-color {
      text-align: center;
      ul {
        padding: 0;
        margin: 0;
        display: inline-block;
        li {
          margin: 0;
          display: inline-block;
          width: 25px;
          height: 25px;
          border: 1px solid $color-white;
          box-shadow: 0 0 0 2px $color-white inset;
          &.active {
            border-color: $color-black; } } } }
    &-th {
      height: 0;
      position: relative;
      &-wrapper {
        position: absolute;
        background: $color-white;
        left: -11px;
        right: -11px;
        font-size: 12px;
        color: #828282;
        padding: 5px 15px 15px 15px;
        top: 55px;
        border: 1px solid #ecedea;
        border-top: 0;
        box-shadow: 0px 22px 30px rgba($primary-color,0.2);
        opacity: 0;
        visibility: hidden;
        transition: all 0.25s;
        text-align: left;
        p {
          margin: 0;
          line-height: 16px; } } } } }

.single-product {
  &__top {
    display: flex;
    justify-content: space-between;
    margin: 0 -15px 30px -15px;
