      height: 35px;
      text-align: center;
      border: 0;
      background: #25925e;
      color: $color-white;
      margin-top: -35px;
      position: absolute;
      right: 0;
      border-radius: 5px;
      font-size: 14px;
      transition: background 0.35s;
      &:hover, &:focus {
        background: $color-green; } }
    &-form {
      position: relative;
