      padding-bottom: 0; }
    &__cart {
      &-popup {
        top: 55px; }
      &-top {
        // height: 55px
        white-space: nowrap;
        margin-top: 20px;
        padding-right: 0;
        width: auto;
        float: right;
        margin-right: 30px;
        .fas {
          font-size: 17px;
          color: $color-green !important; }
        span {
          background: #C4C4C4 !important;
          // position: inherit
          display: inline-block;
          top: -5px;
          color: $primary-color;
          right: inherit;
          vertical-align: -5px;
          margin-left: 5px;
          overflow: hidden;
          width: 25px;
          height: 25px;
          line-height: 25px;
          &:empty {
            width: 1px; } } } }
    &__wrapper {
      &>div {
        &:first-child {
          order: 2; }
        &:nth-child(2) {
