        padding-top: 0;
        background-position: left bottom;
        .container {
          height: 510px; } }
      &Top {
        margin-top: -55px; } } }
  .main-category {
    &__wrapper {
      height: 455px;
      &--row {
        height: 219px; } }
    &__block {
      width: 455px; } }
  .section {
    &__head {
      &>div {
        h1, .h1 {
          font-size: 25px; } } } }
  .advantages {
    &--why {
      padding-left: 15px;
      padding-right: 15px;
      .advantages__block {
