  .single-product__gallery-big-block {
    height: 396px; }
  .single-product__gallery-left::after, .single-product__gallery-left::before {
    height: 13px; }
  body {
    padding-top: 55px; }
  .catalog {
    &__block {
      transform: translateY(0) !important;
      &-th {
        display: none; }
      &-img {
        &>img {
          &.img-first {
            opacity: 1; }
          &.img-default {
            display: none; }
          &.img-hover {
            display: none; } } } } } }

