/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {
  .advantages {
    flex-wrap: wrap;
    &>div {
      width: 50%;
      &+div {
        padding-left: 0; }
      &:last-child, &:nth-last-child(2) {
        padding-top: 15px; } }
    &--why {
      padding-top: 0;
      padding-bottom: 0;
      max-width: 480px;
      margin: 0 auto;
      background: none;
      .advantages__block {
        background: rgba($color-white,0.4);
        height: 50px;
        float: none;
        padding-left: 5px;
        padding-right: 5px; }
      &>div {
        flex-grow: 0;
        &+div {
          border-left: 0;
          padding-left: 0; }
        &:first-child, &:nth-child(3) {
          padding-right: 15px; }
        &:nth-child(2), &:last-child {
          padding-left: 15px; }
        &:nth-child(3), &:last-child {
          padding-top: 15px; } } } }
  .why {
    padding-bottom: 30px;
    padding-top: 30px;
    background: radial-gradient(circle,rgba(255,255,255,0) 0,#d3d3d3 80%); }
  .footer {
    &__center {
      &>div {
        width: 100%;
        &:first-child, &:nth-child(2) {
          display: none; } } }
    &__social {
      text-align: center; }
    &__bottom {
      text-align: center;
      border-top: 0;
      padding-top: 0; }
    &__copyright {
      float: none; }
    &__development {
      float: none;
      padding-top: 15px; }
    &-new {
      &__right, &__center {
        float: none;
        width: 100%; }
      &__right {
        padding-top: 25px; }
      &__center {
        overflow: hidden;
        dl {
          width: 100%;
          padding: 0 !important;
          float: none;
          dt {
            margin: 0; } }
        .toggle {
          .plus {
            display: inline-block; }
          dd {
            overflow: hidden;
            height: 0;
            margin: 0;
            line-height: 31px;
            transform: translate(100%);
            a {
              display: block;
              padding: 0; }
            &:nth-of-type(1) {
              transition: .2s .1s; }
            &:nth-of-type(2) {
              transition: .25s .15s; }
            &:nth-of-type(3) {
              transition: .3s .2s; }
            &:nth-of-type(4) {
              transition: .35s .25s; }
            &:nth-of-type(5) {
              transition: .4s .3s; }
            &:nth-of-type(6) {
              transition: .45s .35s; }
            &:nth-of-type(7) {
              transition: .5s .4s; }
            &:nth-of-type(8) {
              transition: .55s .45s; }
            &:nth-of-type(9) {
              transition: .6s .5s; }
            &:nth-of-type(10) {
              transition: .65s .55s; } }
          &.active {
            dt {
              .plus {
                transform: rotate(225deg); } }

            dd {
              height: 31px;
              transform: translate(0);
              a {
                transition: .4s;
                padding: 0 13px; } } } } } } }
  .main {
    &-slider {
      height: 285px;
      &__block {
        height: 285px;
        .container {
          height: 285px;
          max-width: 480px; } }
      &__img {
        width: 240px;
        margin-left: -15px; }
      &__title {
        font-size: 17px; }
      &__content {
        padding-left: 0; }
      .slick-dots {
        bottom: 15px; }
      &Top {
        &__img {
          float: left;
          width: 100%;
          img {
            width: 240%;
            float: left;
            transform: translateX(-10%); } }
        &__content {
          position: relative;
          transform: translateY(0);
          top: inherit;
          text-align: center;
          padding: 15px 15px 65px 15px;
          left: inherit;
          p {
            max-width: none; } } } } }
  .main-category {
    &>.container {
      max-width: 480px; }
    &__wrapper {
      height: auto; }
    &__block {
      width: 100%;
      position: inherit;
      &:not(:last-child) {
        margin-bottom: 20px; } } }
  .subscription {
    &__wrapper {
      height: 130px; }
    &__body {
      flex-direction: column;
      align-items: flex-start; }
    &__label {
      margin-bottom: 10px; } }
  .menu__mobile {
    ul {
      max-width: none;
      width: calc(100% - 30px); }
    ul li>ul>li>a {
      font-size: 14px; } }
  .shared {
    flex-direction: column;
    &__label {
      padding-right: 0;
      padding-bottom: 10px; }
    &>div {
      &+div {
        padding-left: 0; } } }
  .content {
    &__banner {
      margin-bottom: 20px;
      min-height: 300px;
      &--catalog, &--banner {
        background-size: auto 100%;
        background-position: 41% 100%; } } }
  .tab {
    &-nav li a {
      padding: 10px; }
    &-content {
      padding: 10px; } }
  .loader__model {
    flex-direction: column;
    max-width: 480px;
    &>div {
      &:first-child {
        order: 2; }
      &:last-child {
        order: 1;
        width: 100%;
        padding-left: 0; } } }
  .single-product {
    &__top {
      display: block;
      &>div {
        width: 100%;
        &+div {} } }
    &__gallery {
      padding-left: 0;
      margin-bottom: 15px;
      &-left {
        display: none; }
      .slick-dots {
        li {
          margin: 0; } } }
    &__table {
      float: left;
      width: 50%; }
    &__quality {
      float: right;
      width: calc(50% - 30px);
      border: 0;
      padding: 0; }
    &__desc {
      &-block {
        display: block;
        @include clearfix;
        &>div {
          padding: 0 !important;
          margin: 0;
          width: 100%; }
        img {
          margin-right: -10px;
          margin-top: 0; }
        &--reverse {
          img {
            margin-left: -10px;
            margin-right: 0; } }
        &:last-child {
          margin-bottom: -10px; }
        &--right {
          display: flex;
          flex-direction: column;
          &:last-child {
            margin-bottom: 5px; }
          &>div {
            &:first-child {
              order: 2;
              p {
                &:last-child {
                  margin-bottom: 0; } } }
            &:last-child {
              padding-bottom: 15px !important;
              order: 1;
              img {
                margin-right: 0; } } } }
        &--left {
          display: flex;
          flex-direction: column;
          &:last-child {
            margin-bottom: 50px; }
          &>div {
            &:first-child {
              order: 1;
              img {
                margin-right: 0;
                padding-bottom: 15px; } }
            &:last-child {
              order: 2;
              p {
                &:last-child {
                  margin-bottom: 0; } } } } } } }
    &__tab {
      .nav {
        overflow: scroll;
        li {
          flex-shrink: 0;
          a {
            padding: 0 10px;
            height: 40px;
            line-height: 40px; } } } }
    &__load {
      &>div {
        width: 100%; } } }
  .cart {
    &__step {
      max-width: 290px;
      margin-left: auto;
      margin-right: auto;
      &-block {
        strong {
          width: 25px;
          height: 25px;
          line-height: 23px;
          font-size: 12px; }
        span {
          font-size: 12px; }
        i {
          width: 30px;
          right: -24px;
          height: 1px;
          bottom: inherit;
          top: 13px;
          transform: none;
          &::after {
            border-top-width: 4px;
            border-bottom-width: 3px;
            border-left-width: 8px;
            bottom: -3px; } } } }
    .td-2 {
      vertical-align: top !important; }
    &__name {
      margin-bottom: 10px;
      display: inline-block; }
    &__price {
      margin-bottom: 10px; }
    &__number {
      margin-left: 0;
      margin-bottom: 10px; }
    &__img {
      img {
        width: 44px; } } } }

